wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'


import Vue from 'vue'

Vue.config.productionTip = false
App.mpType = 'app';
import store from './store';

try {
    function isPromise(obj) {
        return (
            !!obj &&
            (typeof obj === "object" || typeof obj === "function") &&
            typeof obj.then === "function"
        );
    }

    // 统一 vue2 API Promise 化返回格式与 vue3 保持一致
    uni.addInterceptor({
        returnValue(res) {
            if (!isPromise(res)) {
                return res;
            }
            return new Promise((resolve, reject) => {
                res.then((res) => {
                    if (res[0]) {
                        reject(res[0]);
                    } else {
                        resolve(res[1]);
                    }
                });
            });
        },
    });

} catch (error) {
}

const app = new Vue({
    ...App
})
app.$mount()